import { Block } from '@ali/4ever-cangjie';
import {
  JsonML,
  getTagName,
  getAttributes,
} from '@ali/4ever-utils';
import { tagToType } from './constants';

export default {
  name: 'table',

  match: (node: JsonML): boolean => {
    return !!tagToType[getTagName(node)];
  },

  convert: (node: JsonML): Block => {
    return Block.create({
      type: tagToType[getTagName(node)],
      data: getAttributes(node) || {},
    });
  },
};
